































import { Vue, Component, Prop, Mixins } from "vue-property-decorator";
import { jobsModule, routerModule } from "@/store/modules";
import { IJob } from "@/store/modules/JobsModule";
import { nextCronExecutionDate } from "@/misc/cron";

@Component({
  components: {}
})
export default class RepositoryOverviewComponent extends Mixins() {
  @Prop({
    type: Array,
    default: () => []
  })
  jobs!: IJob[];

  get extendedJobs() {
    return this.jobs.map(j => ({
      ...j,
      nextDate: nextCronExecutionDate(j.repeatPattern, j.startDate)
    }));
  }

  getMenuItems(job: IJob) {
    return [
      {
        title: this.$t("job.view-logs"),
        to: `/jobs/${job.id}/logs`
      },
      {
        title: this.$t("job.view-repository"),
        to: `/repositories/${job.repoId}`
      }
    ];
  }
}
